.movie-list-area {
  position: absolute;  
  left:0;
  right:0;
  top:40px;
  bottom:0;
  overflow-x:hidden;
  overflow-y:auto;
}

.movie-list-area > ul{
  display:inline-block;
  padding-top:30px;
  width: 100%;
}

.navbar  {
  box-shadow: 0px 5px 6px 0px rgba(0,0,0,0.14);
}
.media {
  float: left; 
  margin-bottom: 2px;
  min-height: 95px;
  max-height: 95px;
}
.media img {
  width: 64px;
  cursor: pointer;
}
.media-body{
  position: absolute;
  pointer-events: none;
  width: 95%;
top: 50%;
left: 0px;
margin-top: -15px;
padding-left: 100px;
}
.media-title {
max-width: 80%;
pointer-events: fill;
cursor: pointer;
}

.media-body .star-icon ,.media-body .fill-star-icon {
position: absolute;
right: 0;
top:5px;
cursor: pointer;
pointer-events: fill;
}

.movie-details {
  position:absolute;
  overflow-x:hidden;
  padding-top:50px;
  left:0;
  right:0;
  top:0;
  bottom:0;
  z-index:99999;
  background-color: rgba(250,250,250,0.95);
  overflow-y: auto
}

.movie-details .x-squar-fill-icon {
  position: absolute;
  right:20px;
  top:20px;
  cursor: pointer;
}
.spinner-border{
  position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -20px;
    margin-top: -20px;
}
